$icon-close: 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTAiIGhlaWdodD0iMTAiIHZpZXdCb3g9IjAgMCAxMCAxMCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cGF0aCBmaWxsPSIjNzE3MTcxIiBkPSJNOCAwbC0zIDMtMy0zLTIgMiAzIDMtMyAzIDIgMiAzLTMgMyAzIDItMi0zLTMgMy0zeiIvPjwvc3ZnPgo=';

$icon-warning: 'data:image/svg+xml;base64,';
$icon-info: 'data:image/svg+xml;base64,';


@mixin alert($bg, $bdr, $icon, $text: $pb-gray-700) {
  color: $text;
  background-color: $bg;
  background-image: url($icon);
  border-color: $bdr;
  i {
    color: $bdr;
  }
}

.doc-body-alert{
  // all alerts
  .alert {
    border-radius: 0;
    background-position: 15px 15px;
    background-repeat: no-repeat;
    padding: 15px 15px 0 50px;
    margin: 0;
  }
  .alert-warning {
    @include alert(#fff, #fff, $icon-warning);
    color: #717171;
  }

  .alert-info {
    @include alert(#fff, #fff, $icon-info);
    color: #717171;
  }
}

.table-open {
  margin-top: 30px;
}
.doc-body-snippet {
  height: auto;
  margin-bottom: 10px;

  .CodeMirror {
    max-height: 41rem;
    border-bottom: solid 1px $pb-light-gray;
    border-left: solid 1px $pb-light-gray;
    border-right: solid 1px $pb-light-gray;
  }

  .CodeMirror-gutters {
    background-color: $pb-light-gray;
  }

  .CodeMirror-lines {
    padding: 20px 0;
  }

  .CodeMirror-line {
    padding: 0 20px;
  }

  .tab-content > .tab-pane {
    padding: 0;
    visibility: hidden;
    display: block;
    position: absolute;
  }

  .tab-content > .active{
    position: static;
    visibility: visible;
  }

  .nav {
    cursor: default;
  }
}

.pb-demo {
  padding-top: 15px;
  margin-bottom: 30px;

  .pb-demo-image a {
    margin-top: 15px;
    position: relative;
    display: inline-block;
    background-color: $close-color;

    img {
      border: 1px solid $pb-gray;
      transition: opacity .2s;
      opacity: 1;
    }

    &:before {
      font-family: "PrecisionSans_W_Rg", "Helvetica Neue", Arial, sans-serif;
      content: 'View Demo';
      font-size: 18px;
      display: block;
      color: #fff;
      @include vertical-align(absolute);
      padding: 6px 12px;
      opacity: 0;
      transition: opacity .3s;
    }

    &:hover {
      &:before {
        opacity: 1;
      }
      img {
        opacity: .2;
      }
    }
  }
}
